const short = document.querySelector('.term_short')
const medium = document.querySelector('.term_medium')
const long = document.querySelector('.term_long')

const details = [].slice.call(document.getElementsByClassName('dynamic__details'))


function initTabs(block) {
    const tabs = [].slice.call(block.getElementsByClassName('dynamic__tab'))
    const shorts = [].slice.call(block.getElementsByClassName('dynamic__content'))

    tabs.forEach((element, index) => {
        element.addEventListener('click', e => {
            e.currentTarget.classList.add('dynamic__tab_active')
            shorts[index].classList.add('dynamic__content_active')

            for (let i = 0; i < tabs.length; i++) {
                if (i !== index) {
                    let element = tabs[i];
                    element.classList.remove('dynamic__tab_active')
                    shorts[i].classList.remove('dynamic__content_active')
                }
            }
        })
    })
}


details.forEach(elem => {
    const items = elem.getElementsByClassName('dynamic__detail')
    const values = []

    for (let item of items) {
        values.push(Number(item.querySelector('p:last-child').innerText.replace('%', '')))
    }

    let max = Math.max(...values)

    for (let item of items) {
        const value = item.querySelector('p:last-child').innerText.replace('%', '')
        item.querySelector('.dynamic__range').style.width = `calc((${value} * 100%) / ${max})`
    }
})


initTabs(short)
initTabs(medium)
initTabs(long)